* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul,ol {
  list-style: none;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

.app-input {
  border: none;
  box-shadow: none;
}

body {
  font-family: sans-serif;
  font-size: 14px;
  color: #9b9b9b;
  font-weight: 400;
  line-height: 1.4;
  display: flex;
  flex-direction: column;
}

.page-container {
  min-height: 100vh;
}

.app-header {
  background-image: linear-gradient(to right, crimson, coral);
  color: white;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-title {
  font-size: 18px;
  text-transform: uppercase;
}

.app-main-content {
  padding: 20px;
  display: flex;
}

.app-searcher-favourites {
  flex-basis: 200px;
}

.app-searcher {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  padding-bottom: 20px;
  border-bottom: 2px solid crimson;
}

.app-label {
  color: #9b9b9b;
  margin-bottom: 10px;
}

.app-input {
  height: 30px;
  width: 60%;
  padding-left: 5px;
  margin-bottom: 10px;
  border-bottom: 1px solid crimson;
}

.btn {
  color: white;
  padding: 2px 10px;
  margin-left: 5px;
  margin-bottom: 10px;
  border-radius: 2px;
}

.btn-search {
  background-color: crimson;
}

.btn-reset-all {
  background-color: black;
}

.favourites {
  margin-top: 20px;
}

.favourites__title {
  color: crimson;
  font-size: 16px;
  margin-bottom: 5px;
}

.favourites__description {
  margin-bottom: 5px;
}

.favourites__list {
  display: flex;
  flex-wrap: wrap;
}

.preview--favourite {
  width: 80px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: crimson;
  background-color: coral;
  cursor: pointer;
}

.preview--favourite img {
  width: 100%;
  margin-bottom: 5px;
}

.preview--favourite h3 {
  font-size: 12px;
  text-align: center;
}

.btn-reset {
  color: white;
  background-color: crimson;
  width: 20px;
  height: 20px;
  border-radius: 50px;
  padding: 3px;
}

.results {
  margin-left: 20px;
  padding-left: 20px;
  border-left: 2px solid crimson;
}

.results__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.results__title {
  margin-bottom: 10px;
  color: crimson;
  font-size: 16px;
  text-align: center;
}

.show-card {
  color: coral;
  background-color: crimson;
  padding: 10px;
  margin: 5px;
  cursor: pointer;
}

.show-card--favourite {
  color: crimson;
  background-color: coral;
}

.app-result {
  height: 100%;
}

.app-footer {
  color: crimson;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  margin-top: auto;
}

@media all and (min-width: 1200px) {
  .page-container {
    max-width: 1600px;
    margin: 0 auto;
  }
}
